.lhq-alert-shroud {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.15);
}
.lhq-alert-container {
    position: fixed;
    top: 50vh;
    left: 50vw;
    transform: translate(-50%, -50%);
    text-align: center;
    border-radius: 0.5em;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    padding: 0.75em 2.5em;
    max-width: 550px;
    background-color: white;
    display: flex;
    flex-direction: column;
}
.lhq-alert-title, .lhq-alert-content, .lhq-alert-button-confirm {
    margin: 0.35em 0;
}
.lhq-alert-container .lhq-alert-button-confirm {
    border-radius: 0.5em;
}
.lhq-alert-container .lhq-alert-content {
    word-wrap: break-word;
}
.lhq-alert-container .lhq-alert-button-confirm {
    color: white;
    background-color: #18a718;
    border: 1px solid #18a718;
    padding: 0.25em 0.5em;
}
.lhq-alert-container .lhq-alert-button-confirm:hover {
    box-shadow: 0.2em 0.3em 0.3em rgba(0, 0, 0, 0.23);
    cursor: pointer;
}
.lhq-alert-container .lhq-alert-button-confirm:active {
    box-shadow: inset 0.2em 0.3em 0.3em rgba(0, 0, 0, 0.23);
}
.lhq-confirm-shroud {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.15);
}
.lhq-confirm-container {
    position: fixed;
    top: 50vh;
    left: 50vw;
    transform: translate(-50%, -50%);
    text-align: center;
    border-radius: 0.5em;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    padding: 0.75em 2.5em;
    max-width: 550px;
    background-color: white;
    display: flex;
    flex-direction: column;
}
.lhq-confirm-title, .lhq-confirm-content {
    margin: 0.35em 0;
}
.lhq-confirm-button-confirm, .lhq-confirm-button-cancel {
    border-radius: 0.5em;
}
.lhq-confirm-container .lhq-confirm-content {
    word-wrap: break-word;
}
.lhq-confirm-button {
    margin: 0.5em auto;
}
.lhq-confirm-button .lhq-confirm-button-confirm {
    margin-right: 0.5em;
    color: white;
    background-color: #18a718;
    border: 1px solid #18a718;
    padding: 0.25em 0.5em;
}
.lhq-confirm-button .lhq-confirm-button-cancel {
    color: white;
    background-color: rgb(218 63 63);
    border: 1px solid rgb(218 63 63);
    padding: 0.25em 0.5em;
}
.lhq-confirm-button-confirm:hover, .lhq-confirm-button-cancel:hover {
    box-shadow: 0.2em 0.3em 0.3em rgba(0, 0, 0, 0.23);
    cursor: pointer;
}
.lhq-confirm-button-confirm:active, .lhq-confirm-button-cancel:active {
    box-shadow: inset 0.2em 0.3em 0.3em rgba(0, 0, 0, 0.23);
}
.lhq-toast-container {
    position: fixed;
    left: 50vw;
    transform: translate(-50%, 0);
    transition: top .8s;
    box-shadow: 0px 0px 0.5em #000;
    border-radius: 0.5em;
    padding: 0.5em 1em;
    margin: 0;
    background-color: white;
}
.lhq-toast-container .lhq-toast-content {
    padding: 0;
    margin: 0;
}
.container {
    width: 65vw;
    margin: 5em auto;
}
.container .title {
    text-align: center;
    padding: 1em 0em;
}
.main {
    display: flex;
    flex-direction: column;
    width: 50vw;
    margin: 0 auto;
}
.alert, .confirm, .toast {
    display: flex;
    flex-direction: column;
    margin: 0.5em 0;
}

.alert-title, .confirm-title, .toast-title {
    font-size: 24px;
    margin: 0.5em;
}

.alert-button, .confirm-button, .toast-button {
    font-size: 20px;
    padding: 0.2em;
    cursor: pointer;
}
